}
.App{
  width: 100%;
 
    
}
.whatsnew__items-icons:hover,
.trending-holder-icon:hover,
.spotlight-holder-icon:hover,
.top__items-holder-item:hover,
.fresh__items-holder-item:hover,
.presets-holder-icon:hover{
  background-color: rgba(51, 51, 51, 0.574);
  cursor: pointer;
}
.presets__holder-top span:hover,
.whatsnew__genre span:hover,
.trending__holder-top span:hover,
.spotlight__title span:hover,
.selections__holder-top  span:hover{
  background-color: rgba(0, 0, 0, 0.5);
  color: rgb(255, 255, 255);
  cursor: pointer;
  border: 1px rgb(25, 25, 25) solid;
}

.whatsnew__items-icon-play,
.presets__items-icon-play,
.trending__items-icon-play{
  display: none;
  position: absolute;
  
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.5);
  z-index: 1;
  height: 170px;
  width: 170px;
  border-radius: 5px;
  
}
.topsample__items-icon-play,
.selection__holder-icon-play{
  display: none;
  position: absolute;
  
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.5);
  z-index: 1;

}
.topsample__items-icon-play{
  width: 50px;
  height: 50px;
  display: none;
  align-items: center;
  justify-content: center;
}
.topsample__items-icon-play span{
  display: flex;
  align-items: center;
  background: rgb(66, 52, 219);
  justify-content: center;
  height: 35px;
  width: 35px;
  border-radius: 50%;
}
.topsample__items-icon-play svg{
  width: 50%;
  height: 50%;
}
.top__items-holder-item:hover .topsample__items-icon-play,
.whatsnew__items-icons:hover .whatsnew__items-icon-play,
.presets-holder-icon:hover .presets__items-icon-play,
.trending-holder-icon:hover .trending__items-icon-play,
.selections__holder-icon:hover .selection__holder-icon-play{
display: flex;
}

.whatsnew__items-icon-play span,
.presets__items-icon-play span,
.trending__items-icon-play span,
.selection__holder-icon-play span{
 
  width: 45px;
  height: 44px;
  background: rgb(66, 52, 219);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin:0;
}

.whatsnew__items-icon-play svg,
.presets__items-icon-play svg,
.trending__items-icon-play svg,
.selection__holder-icon-play svg{
  
  width: 25px;
  height: 25px;

}

